import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Requirement } from '../_models/Requirement';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { RequirementData } from '../_models/RequirementData';
import { Offer } from '../_models/Offer';
import { ChatBox } from '../_models/ChatBox';
import { ShareDealData } from '../_models/ShareDealData';
import { OrderData } from '../_models/OrderData';
import { ShortfallList } from '../_models/ShortfallList';
import { DealDataList } from '../_models/DealDataList';
import { POList } from '../_models/POList';

@Injectable({
  providedIn: 'root'
})
export class RequirementService {

  baseUrl = environment.apiUrl + 'requests/';

constructor(private http: HttpClient) { }

  sendRequirementPost(request: Requirement) {
    return this.http.post(this.baseUrl + 'requirementpost', request);
  }
  sendPurchaseOrderPost(request) {
    return this.http.post(this.baseUrl + 'addpurchaseorderpost', request);
  }
  addOrder(request) {
    return this.http.post(this.baseUrl + 'addOrder', request);
  }
  sendMessagePost(request: ChatBox) {
    return this.http.post(this.baseUrl + 'sendmessagepost', request);

  }
  ApproveDeals(request: RequirementData) {
    return this.http.post(this.baseUrl + 'approvedirectdeals', request);

  }
  EditReqDataPost(request: RequirementData) {
    console.log(request);
    return this.http.post(this.baseUrl + 'editreqdatapost', request);

  }
  sendDealsPost(request: Requirement) {
    return this.http.post(this.baseUrl + 'dealspost', request);
  }
  RemoveDealsPost(request: Requirement) {
    return this.http.post(this.baseUrl + 'removedealspost', request);
  }
  getRequirements(): Observable<RequirementData[]> {
    return this.http.get<RequirementData[]>(this.baseUrl + 'getrequirements/' + Number(localStorage.getItem('UserId')));
  }
  getPurchaseOrders(): Observable<POList[]> {
    return this.http.get<POList[]>(this.baseUrl + 'getpurchaseorder');
  }
  getOrderss(): Observable<OrderData[]> {
    return this.http.get<OrderData[]>(this.baseUrl + 'getorder');
  }
  getShortFalls(): Observable<ShortfallList[]> {
    return this.http.get<ShortfallList[]>(this.baseUrl + 'getshortfall');
  }
  getRejectdeals(): Observable<DealDataList[]> {
    return this.http.get<DealDataList[]>(this.baseUrl + 'getrejecteddeal');
  }
  getRemoveddeals(): Observable<DealDataList[]> {
    return this.http.get<DealDataList[]>(this.baseUrl + 'getremoveddeal');
  }
  getclosedeals(): Observable<DealDataList[]> {
    return this.http.get<DealDataList[]>(this.baseUrl + 'getclosedeal');
  }
  loadSharedData(): Observable<ShareDealData[]> {
    return this.http.get<ShareDealData[]>(this.baseUrl + 'getshareddeals');
  }
  getOffers(): Observable<Offer[]> {
    return this.http.get<Offer[]>(this.baseUrl + 'getOffers');
  }
  ApproveRejectDealDeals(request) {
    return this.http.put(this.baseUrl + 'approverejectdeals/', request);
  }
  CloseRequirement(request) {
    return this.http.put(this.baseUrl + 'closerequirement/', request);
  }
  CloseDeals(request) {
    return this.http.put(this.baseUrl + 'closedeals/', request);
  }
  UpdatePOPost(request) {
    return this.http.put(this.baseUrl + 'updatepopost/', request);
  }
  viewChats(request: ChatBox) {
    console.log('request');
    console.log(request);
    return this.http.post(this.baseUrl + 'viewChats', request);
  }
  sendChatPost(request: Requirement) {
    console.log(request);
    return this.http.post(this.baseUrl + 'sendmessagepost', request);
  }
  ShareReqPost(request) {
    console.log(request);
    return this.http.post(this.baseUrl + 'sharereqpost', request);
  }
  RejectStock(request) {
    console.log(request);
    return this.http.put(this.baseUrl + 'rejectstock', request);
  }
}
